import React from "react";
import { RiArrowLeftUpLine } from "react-icons/ri";
import stores from "../../../types/store.type";
 const stores:stores = [
  {
    name: "حدائق القبة",
    address: "شارع مصر والسودان",
    link: "https://maps.app.goo.gl/Cv5Y81SfP5dJu6cU9"
  }
 ]
export default function Stores() {
  return (
    <section className="stores ">
      <div className="container grid gird-cols-1 md:grid-cols-2  gap-4">
        {/* it will be added by database */}
        <a
          href="https://maps.app.goo.gl/Cv5Y81SfP5dJu6cU9"
          className="p-4 bg-white rounded-md hover:border shadow-sm flex justify-between items-center  hover:border-sColor"
          target="_blank"
        >
          <article className="flex flex-col items-center gap-1">
            <h2 className="text-2xl font-bold text-gray-700">حدائق القبة</h2>
            <p className="text--sm text-gray-600">شارع مصر والسودان</p>
          </article>
          <button aria-label="icon go to link">
            <RiArrowLeftUpLine className="text-3xl text-sColor" />
          </button>
        </a>
      </div>
    </section>
  );
}
